import { Injectable } from '@angular/core';
import { EVENT_LIST } from "../mocks/events";
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';

@Injectable()

export class EventService {

	event:any; // array of object of type any for storing events
	constructor(){}

	getEvents():Observable<any>{
		return Observable.of(EVENT_LIST.filter(event => this.event = event));
	}
}


