import { Component, OnInit, ViewChild } from '@angular/core';
import { DayPilot, DayPilotCalendarComponent } from "daypilot-pro-angular";
import { EventService } from "../providers/event.service";
import { Events } from "../models/events.interface";

@Component({
  selector: 'calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.css']
})
export class CalendarComponent implements OnInit {

	@ViewChild('calendar') calendar: DayPilotCalendarComponent;

	events :Events; // Array object for storing events of type events
	
	constructor(private es:EventService) {}
	
	// Sidebar Calendar View Type
	navigatorConfig = {
		selectMode: "week"
	};

	// Event Calendar Configuration for today date and calendar type
	calendarConfig = {
		startDate: DayPilot.Date.today(),
		viewType: "Week"
	};

	ngOnInit(){
		// Getting Events Call
		this.getEvents();
	}
	
	// Method for getting all events
	getEvents():void{
		this.es.getEvents().subscribe((data:Events) => this.events = data);
	}
}


